@keyframes loading {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(359deg);
    }
}

.m-voteloading {
    width: 24px;
    height: 24px;
    display: inline-block;
    background-image: url('./voteloading@3x.png');
    background-size: 100% 100%;
    animation: loading 0.6s linear infinite;
}
