// 背景图
.bg-normal(@img, @ext: 'png', @size: 100% 100%, @position: center, @repeat: no-repeat) {
    background: @position ~'/' @size @repeat url('@{img}');
}

body {
    
    
}

.m-record {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    border-top-left-radius: .2667rem;
	border-top-right-radius: .2667rem;
    .empty {
        display: block;
        margin: 58px auto 0;
        width: 5rem;
        height: 2rem;
        .bg-normal('./img/1bb2e8583039d7cfec0eab604addc1d5.png');
    }

    .back {
        position: absolute;
        top: 14px;
        left: 17px;
        width: .64rem;
        height: .64rem;
        .bg-normal('./img/e190a655d6839903752e3643f225a413.png');
    }

    .bg {
        width: 100%;
        height: 17.83rem;
        .bg-normal('./img/33bb36a4f43c841dc07715ebc4e45518.png');
        border-top-left-radius: .2667rem;
	    border-top-right-radius: .2667rem;
    }

    .tab {
        position: absolute;
        top: 1.56rem;
        left: 0;
        right: 0;
        height: 25px;
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .tab-wrapper {
        padding: 8px;
    }

    .tab-left {
        margin-left: 129px;
        width: 1rem;
        height: .28rem;
    }
    .tab-left.tab1 {
        .bg-normal('./img/a1eeaf128f12a39808b91e152c96cb33.png');
    }

    .tab-left.tab1Active {
        .bg-normal('./img/a14c32d66f5825e6f82a0d816f5a9875.png');
    }

    .tab-right {
        width: 1rem;
        height: .28rem;
        margin-left: 9px;
    }

    .tab-right.tab2 {
        .bg-normal('./img/d9c9deedd883a679a551b53dbd9ed21f.png');
    }

    .tab-right.tab2Active {
        .bg-normal('./img/75198f247684733b48fa4f58547bf8d6.png');
    }

    .tip {
        position: absolute;
        top: 3.8rem;
        left: 0;
        width: 100%;
        text-align: center;
        height: 10px;
        font-size: 10px;
        color: #cdcdcd;
        line-height: 10px;
    }
}

.m-record-list {
    position: absolute;
    top: 163px;
    left: 0;
    width: 100%;
    height: calc(100vh - 163px);
    box-sizing: border-box;
    overflow-y: auto;

    .item {
        display: flex;
        width: 286px;
        height: 59px;
        margin-left: 40px;
        position: relative;

        &.fail {
            height: 39px;
        }

        .left {
            width: 43px;
            height: auto;
            position: relative;
            display: flex;
            justify-self: center;
            align-items: center;
            background-color: rgba(28, 78, 105, 0.4);
        }

        .right {
            padding: 7px 0 0 12px;
            flex: 1;
            height: 100%;
            box-sizing: border-box;
            background-color: rgba(28, 78, 105, 0.6);
            margin-left: 3px;
        }

        .title {
            height: 10px;
            font-size: 10px;
            font-weight: bold;
            color: #fff;
            line-height: 10px;
        }

        .detail {
            margin-top: 2px;
            height: 15px;
            line-height: 15px;
            font-size: 9px;
            color: #afd0ff;
        }

        .detail-record {
            margin-top: 2px;
            min-height: 15px;
            line-height: 15px;
            font-size: 9px;
            color: #afd0ff;
        }

        .time {
            margin-top: 6px;
            font-size: 8px;
            height: 12px;
            color: #6b8593;
            line-height: 12px;
        }

        .star {
            width: 47px;
            height: 47px;
            object-fit: contain;
        }

        .status {
            position: absolute;
            bottom: 0;
            right: -12px;
            width: 1rem;
            height: .76rem;
            object-fit: contain;
        }
        .status.success {
            .bg-normal('./img/b47998b8e3105b3607a99ef5169d7a8b.png');
        }
        .status.fail {
            .bg-normal('./img/8f301fc07e664c4a62347dc3f1f54e47.png');
        }
    }

    .item-record {
        height: auto;
        
        .right {
            padding-bottom: 8px;
        }
    }

    .item + .item {
        margin-top: 11px;
    }

    .notPlay {
        position: absolute;
        top: 6px;
        right: 17px;
        height: 16px;
        font-size: 9px;
        color: #778993;
        line-height: 15px;
    }
}

.m-voteloading {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}