#dark-toast {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    padding: 5px 10px;
    background: rgba(29, 29, 29, 0.73);
    border-radius: 5px;
    color: #fff;
    text-align: center;
    min-width: 100px;
    pointer-events: none; // 点击穿透

    span {
      font-size: 12px;
      line-height: 1;
    }
  }
  @keyframes rotate-loop {
    from {
      transform: rotateZ(0deg);
    }
    to {
      transform: rotateZ(360deg);
    }
  }